"use client"

import { useEffect, useState } from 'react'

export function MouseTrail() {
  const [dots, setDots] = useState<{ x: number; y: number; id: number }[]>([])
  const [isDesktop, setIsDesktop] = useState(false)

  useEffect(() => {
    // Only enable on desktop
    setIsDesktop(window.matchMedia('(min-width: 768px)').matches)
    
    if (!isDesktop) return

    const maxDots = 15
    let timeoutId: NodeJS.Timeout
    let lastDotTime = 0
    const dotInterval = 20 // Minimum time between dots in ms

    const handleMouseMove = (e: MouseEvent) => {
      const now = Date.now()
      if (now - lastDotTime < dotInterval) return
      lastDotTime = now

      setDots(prev => {
        const newDots = [
          ...prev,
          { 
            x: e.clientX, 
            y: e.clientY,
            id: now // Use timestamp as unique ID
          }
        ]
        if (newDots.length > maxDots) {
          return newDots.slice(1)
        }
        return newDots
      })

      // Clear dots after no movement
      clearTimeout(timeoutId)
      timeoutId = setTimeout(() => {
        setDots([])
      }, 1000)
    }

    window.addEventListener('mousemove', handleMouseMove)
    return () => {
      window.removeEventListener('mousemove', handleMouseMove)
      clearTimeout(timeoutId)
    }
  }, [isDesktop])

  if (!isDesktop) return null

  return (
    <>
      {dots.map((dot, i) => (
        <div
          key={dot.id}
          className="cursor-trail"
          style={{
            left: dot.x - 4, // Center the dot (half of width)
            top: dot.y - 4,  // Center the dot (half of height)
            opacity: (i + 1) / dots.length,
            transform: `scale(${(i + 1) / dots.length})`,
          }}
        />
      ))}
    </>
  )
}
